@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.productWrap {
    .empty {
        margin-top: 50px;
    }
}
.product {
    display: flex;
    margin-top: 50px;
    font-size: 2.25rem;
    @media (max-width: 1200px) {
        font-size: 1.7rem;
    }
    @media (max-width: $breakpoints768) {
        font-size: 1.6rem;
        flex-wrap: wrap;
        border-bottom: 1px solid #f7f7f7;
    }
    &:last-child {
        border-bottom: none;
    }
    &:hover {
        .delete {
            color: $ebonite !important;
        }
    }

    .description {
        flex-wrap: nowrap;
        flex: 1;
        display: flex;
        align-items: center;
        @media (max-width: $breakpoints768) {
            width: 100%;
            flex: none;
            align-items: flex-start;
        }
        @media (max-width: $breakpoints480) {
            width: 100%;
            flex: none;
            font-size: 1.3rem;
            line-height: 1.7rem;
            align-items: flex-start;
        }
        .img {
            width: 100px;
            height: 100px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 40px;
            @media (max-width: $breakpoints480) {
                width: 70px;
                height: 70px;
                margin-right: 15px;
            }
            img {
                object-fit: contain;
                max-width: 100%;
                max-height: 100%;
            }
        }
        .name {
            flex: 1;
            display: flex;
            margin-right: 50px;
            max-height: 150px;
            overflow: hidden;
            text-overflow: ellipsis;
            @media (max-width: $breakpoints768) {
                margin-right: 0;
                max-height: 105px;
            }
            @media (max-width: $breakpoints480) {
                margin-right: 0;
                max-height: 85px;
            }
        }
        .mobileDelete {
            user-select: none;
            display: none;
            cursor: pointer;
            font-size: 2.25rem;
            transform: rotate(45deg);
            align-items: center;
            padding-bottom: 13px;
            margin: -6px 0 0 0;
            @media (max-width: $breakpoints768) {
                display: flex;
            }
        }
    }
    .control {
        flex-wrap: nowrap;
        display: flex;
        @media (max-width: $breakpoints768) {
            margin-top: 20px;
            padding-bottom: 40px;
            width: 100%;
        }
        .quantityControl {
            display: flex;
            align-items: center;
            .quantity {
                user-select: none;
                width: 50px;
                text-align: center;
                font-weight: 600;
                font-size: 25px;
                @media (max-width: $breakpoints768) {
                    width: 40px;
                    font-size: 20px;
                }
            }
            .quantityBtn {
                user-select: none;
                font-size: 25px;
                cursor: pointer;
                display: flex;
                width: 23px;
                height: 18px;
                background-color: $ebonite;
                font-size: 26px;
                font-weight: 600;
                justify-content: center;
                align-items: center;
                padding-bottom: 5px;
                color: white;
                border-radius: 5px;
                &.inactive {
                    background-color: $lightGrey;
                }
            }
        }
        .price {
            display: flex;
            width: 200px;
            justify-content: flex-end;
            align-items: center;
            padding-right: 20px;
            &::after {
                content: " ₽";
                margin-left: 10px;
            }
            @media (max-width: 1200px) {
                width: 150px;
            }
            @media (max-width: $breakpoints768) {
                width: 100px;
                padding-right: 10px;
                flex: 1;
            }
        }
        .delete {
            user-select: none;
            display: flex;
            cursor: pointer;
            font-size: 2.25rem;
            transform: rotate(45deg);
            align-items: center;
            padding-bottom: 13px;
            color: $lightHover;
            @media (max-width: $breakpoints768) {
                display: none;
            }
        }
    }
}

.total {
    text-align: right;
    font-size: 2.25rem;
    margin-top: 36px;
    @media (max-width: $breakpoints768) {
        font-size: 1.7rem;
    }
    @media (max-width: $breakpoints480) {
        font-size: 1.4rem;
    }
    span {
        margin-left: 50px;
        color: $orange;
        @media (max-width: $breakpoints768) {
            margin-left: 15px;
        }
        &::after {
            content: "₽";
            margin-left: 10px;
            @media (max-width: $breakpoints768) {
                margin-left: 2px;
            }
        }
    }
}

.mainBtnWrap {
    margin-top: 36px;
    text-align: right;
    :global(.mainBtn) {
        display: inline-block;
    }
}
