.mobileNavBar {
  display: none;
  width: 100%;
  bottom: 0;
  position: fixed;
  background: #ffffff;
  -webkit-box-shadow: 0px 0px 12px #d1d1d180;
          box-shadow: 0px 0px 12px #d1d1d180;
  border-radius: 30px;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  z-index: 10;
