.mobileNavBar .item :global(.current)::after {
  content: " ";
  position: absolute;
  bottom: 22px;
  right: calc(50% - 3px);
  background-color: #2e3563;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  -webkit-box-shadow: 0 -19px 21px 4px #060a56c4;
          box-shadow: 0 -19px 21px 4px #060a56c4;
}

.mobileNavBar .item :global(.current) svg {
  margin-top: -7px;
}

.mobileNavBar .item :global(.current) svg path {
  fill: #2e3563;
}

.mobileNavBar .item.active.mobileNavBar__item__basket::before {
  left: calc(50% + 5px) !important;
}

.mobileNavBar .item.active.mobileNavBar__item__user::before {
  left: calc(50% + 0px) !important;
}

.mobileNavBar .item.active::before {
  content: " ";
  position: absolute;
  top: 23px;
  left: calc(50% + 2px);
  background-color: #fdc535;
  width: 10px;
  height: 10px;
  border-radius: 6px;
  border: 2px solid #ffffff;
}

