body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #37393b;
}

@media (max-width: 480px) {
  body {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
  }
}

input,
textarea {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #f5f7ff;
  padding: 8px 15px;
  border-radius: 10px;
  border: 1px solid #f5f7ff;
}

input.error,
textarea.error {
  color: #fa5a2a;
  border: 1px solid #fa5a2a;
}

input::-webkit-input-placeholder {
  color: #9099ad;
}

input:-ms-input-placeholder {
  color: #9099ad;
}

input::-ms-input-placeholder {
  color: #9099ad;
}

input::placeholder {
  color: #9099ad;
}

p {
  margin-top: 30px;
  line-height: 24px;
}

@media (max-width: 480px) {
  p {
    line-height: 18px;
  }
}

.h2MarginTop {
  margin-top: 50px;
}

@media (max-width: 800px) {
  .h2MarginTop {
    margin-top: 20px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 60px;
  max-width: 1440px;
}

@media (max-width: 800px) {
  .container {
    padding: 0 30px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 16px;
  }
}

.home_header {
  margin-top: 30px;
}

.sliderTitle {
  margin-top: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
}

@media (max-width: 800px) {
  .sliderTitle {
    margin-top: 28px;
  }
}

.sliderTitle h2 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 20px;
}

@media (max-width: 800px) {
  .sliderTitle h2 {
    font-size: 1.65rem;
  }
}

@media (max-width: 480px) {
  .sliderTitle h2 {
    font-size: 1.125rem;
  }
}

.sliderTitle span {
  white-space: nowrap;
  cursor: pointer;
  font-size: 1.5rem;
  color: #2e3563;
}

.sliderTitle span:hover {
  color: #2e3250;
}

@media (max-width: 800px) {
  .sliderTitle span {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .sliderTitle span {
    font-size: 0.625rem;
  }
}

.mainBtn {
  display: block;
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: -webkit-gradient(linear, left top, right top, from(#436397), to(#7dafff));
  background: linear-gradient(90deg, #436397 0%, #7dafff 100%);
  border-radius: 8px;
  padding: 15px 88px;
  color: white;
}

.mainBtn:hover {
  background: -webkit-gradient(linear, left top, right top, from(#37507a), to(#6e9ae0));
  background: linear-gradient(90deg, #37507a 0%, #6e9ae0 100%);
}

@media (max-width: 920px) {
  .mainBtn {
    padding-right: 40px;
    padding-left: 40px;
  }
}

@media (max-width: 480px) {
  h1,
  .h1 {
    font-size: 2.8rem;
  }
  h2,
  .h2 {
    font-size: 1.33rem;
    font-weight: 500;
  }
  h3,
  .h3 {
    font-size: 1.125rem;
    font-weight: 500;
    margin-top: 17px;
  }
  h4,
  .h4 {
    font-size: 0.875rem;
    font-weight: 400;
    margin-top: 11px;
  }
  h5,
  .h5 {
    font-size: 0.5rem;
  }
}
