@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.popupWrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #00000031;
}

.parent {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #00000031;
    display: flex;
    height: 100%;
    align-items: center;
    z-index: 10;
    .wrap {
        flex: 1;
        text-align: center;
        .centerWindow {
            padding: 0 15px;
            max-height: 100vh;
            display: flex;
            justify-content: center;
        }
    }
}
