@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.deliverWrap {
    color: $corp_inst;

    h4 {
        max-width: 345px;
    }
    ul {
        margin-top: 16px;
        list-style: inside;
        font-weight: 500;
        font-size: 16px;
        color: $corpLight;
        @media (max-width: $breakpoints480) {
            font-size: 14px;
            margin-top: 22px;
            margin-bottom: 22px;
        }
    }
    .deliverSection {
        display: flex;
        color: $corp_inst;
        @media (max-width: $breakpoints768) {
            flex-direction: column;
        }
        .delivery {
            margin-right: 120px;
            @media (max-width: 1000px) {
                margin-right: 60px;
            }
            @media (max-width: $breakpoints768) {
                margin-right: 0;
            }
        }
    }
    .attention {
        color: $orange;
    }
}
