@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";
.product {
    margin-top: 32px;
    display: flex;
    @media (max-width: $breakpoints768) {
        display: none;
    }

    .photo {
        flex-basis: 400px;
        margin-right: 100px;
        @media (max-width: 1200px) {
            flex-basis: 300px;
            margin-right: 40px;
        }
        .mainPhoto {
            height: 400px;
            text-align: center;
            img {
                object-fit: contain;
                max-width: 100%;
                max-height: 100%;
                height: 100%;
            }
        }
        .thumbnailBar {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            margin-top: 15px;
            :global(.active) {
                border: 1px solid $lightGrey !important;
            }
            .thumbnail {
                width: 68px;
                height: 68px;
                padding: 5px;
                border-radius: 7px;
                border: 1px solid white;

                .item {
                    height: 100%;
                    text-align: center;
                    cursor: pointer;
                    img {
                        object-fit: contain;
                        max-width: 100%;
                        max-height: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
    .detail {
        flex: 1;
        h2 {
            margin-top: 10px;
            @media (max-width: 1200px) {
                font-size: 1.9rem;
            }
        }
        .radioColor,
        .radioModel {
            margin-top: 24px;
        }
        .modelWrap {
            .model {
                margin-right: 84px;
            }
            .model,
            .color {
                vertical-align: top;
                display: inline-block;
                margin-top: 36px;
            }
        }
        .priceWrap {
            margin-top: 78px;
            .price {
                display: inline-block;
                margin-right: 32px;
                &::after {
                    content: " ₽";
                }
            }
            .sale {
                display: inline-block;
                margin-right: 32px;
                font-size: 1.5rem;
                color: $orange;
            }
            .oldPrice {
                display: inline-block;
                text-decoration: line-through;
                color: $lightGrey;
            }
        }
        .mainBtnWrap {
            margin-top: 16px;
            :global(.addBtn) {
                vertical-align: top;
                margin-right: 32px;
                @media (max-width: 920px) {
                    margin-right: 20px;
                }
            }
            .favorite {
                cursor: pointer;
                display: inline-flex;
                vertical-align: top;
                width: 50px;
                height: 54px;
                background-color: $light;
                border-radius: 8px;
                justify-content: center;
                align-items: center;
                &:hover {
                    background-color: $lightHover;
                }
            }
        }
    }
}

.stockWrap {
    margin-top: 46px;
}

.productMobile {
    margin-top: 32px;
    display: none;
    @media (max-width: $breakpoints768) {
        display: block;
    }
    .photo {
        height: 346px;
        text-align: center;
        img {
            object-fit: contain;
            max-width: 300px;
            max-height: 346px;
        }
    }
    .dotsNavigation {
        margin-top: 17px;
        text-align: center;
        display: block;
        padding: 10px;
        li {
            display: inline-block;
            width: 8px;
            height: 8px;
            background-color: $lightGrey;
            margin-right: 8px;
            border-radius: 4px;
        }
        :global(.slick-active) {
            background-color: $orange;
        }
    }
    .article {
        margin-top: 30px;
    }
    h2 {
        margin-top: 12px;
        font-weight: 600;
        font-size: 24px;
        line-height: 30px;
        @media (max-width: $breakpoints480) {
            font-size: 18px;
            line-height: 24px;
        }
    }
    .priceSection {
        display: flex;
        margin-top: 36px;
        font-weight: 600;
        font-size: 30px;
        .priceWrap {
            flex: 1;
            @media (max-width: $breakpoints480) {
                font-weight: 600;
                font-size: 24px;
            }
            .price {
                display: inline-block;
                margin-right: 20px;
                &::after {
                    content: " ₽";
                }
            }
            .sale {
                display: inline-block;
                margin-right: 20px;
                color: $orange;
            }
            .oldPrice {
                display: inline-block;
                text-decoration: line-through;
                color: $lightGrey;
                font-weight: 400;
                font-size: 16px;
                @media (max-width: $breakpoints480) {
                    font-weight: 400;
                    font-size: 14px;
                }
            }
        }
        .favorite {
            width: 20px;
            height: 26px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    .modelWrap {
        margin-top: 27px;
        h3 {
            font-weight: 500;
            font-size: 16px;
        }
        .radioModel {
            margin-top: 5px;
            div {
                font-weight: 600;
                font-size: 14px;
            }
        }
    }
    .color {
        margin-top: 36px;
        .radioColor {
            margin-top: 8px;
        }
    }
}

.mobileBtnSection {
    display: none;
    @media (max-width: $breakpoints768) {
        display: block;
    }
    .stockWrap {
        margin-top: 36px;
        font-size: 16px;
    }
    .mainBtnWrap {
        margin-top: 36px;
        margin-bottom: 36px;
        // :index(.mainBtn) {
        .mainBtn {
            box-sizing: border-box;
            width: 100%;
            @media (max-width: $breakpoints480) {
                padding-right: 30px;
                padding-left: 30px;
            }
        }
    }
}

.info {
    margin-top: 50px;
    :global(.active) {
        color: $corp_inst;
    }
    h3 {
        cursor: pointer;
        display: inline-block;
        margin-right: 60px;
        color: grey;

        @media (max-width: $breakpoints480) {
            font-size: 16px;
            margin-right: 36px;
            padding: 0 0 20px 0;
        }
    }
    .body {
        margin-top: 20px;
        @media (max-width: $breakpoints480) {
            margin-top: 0;
        }
    }
}

.radioModel {
    margin-bottom: -16px;
    :global(.active) {
        background-color: $orange;
        color: white;
    }
    div {
        cursor: pointer;
        display: inline-block;
        padding: 8px 20px;
        background-color: #f5f7ff;
        border-radius: 8px;
        color: $corp_inst;
        word-spacing: normal;
        margin-right: 16px;
        margin-bottom: 16px;
        &:last-child {
            margin-right: 0;
        }
    }
}

.radioColor {
    :global(.active) {
        border: 2px solid black;
    }
    div {
        cursor: pointer;
        display: inline-block;
        width: 34px;
        height: 34px;
        border-radius: 19px;
        border: 2px solid white;
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
    .red {
        background-color: #bb4443;
    }
    .green {
        background-color: #a1c964;
    }
    .blue {
        background-color: #878ec0;
    }
    .white {
        background-color: #f7f7f7;
    }
    .black {
        background-color: #3a3a3a;
    }
    .purple {
        background-color: #5b0082;
    }
}
