.blackFont {
  color: black;
}

.banner {
  border-radius: 20px;
  height: 300px;
  margin-top: 32px;
  background: linear-gradient(280.53deg, #b899fb 51.64%, #919cf8 95.61%);
}

@media (max-width: 800px) {
  .banner {
    margin-top: 25px;
    height: 148px;
  }
}

@media (max-width: 800px) {
  .homeH2 {
    display: none;
  }
}

.categoryCardWrap {
  display: -ms-grid;
  display: grid;
  justify-items: stretch;
  margin-top: 32px;
  gap: 10px;
  -ms-grid-columns: (1fr)[5];
      grid-template-columns: repeat(5, 1fr);
}

@media (max-width: 800px) {
  .categoryCardWrap {
    font-size: 13px;
    margin-top: 20px;
  }
}

@media (max-width: 480px) {
  .categoryCardWrap {
    font-size: 10px;
    overflow: hidden;
  }
}

.categoryCardWrap .categoryCard_item {
  height: 126px;
  cursor: pointer;
  -webkit-box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.08), 0px 2px 6px rgba(130, 169, 188, 0.08);
          box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.08), 0px 2px 6px rgba(130, 169, 188, 0.08);
  text-align: center;
  overflow: hidden;
  padding: 0 10px;
}

@media (max-width: 800px) {
  .categoryCardWrap .categoryCard_item {
    -webkit-box-shadow: none;
            box-shadow: none;
    height: auto;
    padding: 0;
  }
}

.categoryCardWrap .categoryCard_item:hover {
  -webkit-box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.16), 0px 2px 6px rgba(130, 169, 188, 0.16);
          box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.16), 0px 2px 6px rgba(130, 169, 188, 0.16);
}

@media (max-width: 800px) {
  .categoryCardWrap .categoryCard_item:hover {
    -webkit-box-shadow: none;
            box-shadow: none;
  }
}

.categoryCardWrap .categoryCard_item span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@media (max-width: 800px) {
  .categoryCardWrap .categoryCard_item span {
    white-space: normal;
  }
}

.categoryCardWrap .categoryCard_imgWrap {
  height: 60px;
  width: 110px;
  line-height: 60px;
  margin: 15px auto 13px auto;
}

@media (max-width: 800px) {
  .categoryCardWrap .categoryCard_imgWrap {
    width: 60px;
    background-color: #f5f7ff;
    border-radius: 30px;
    margin: 0 auto 4px auto;
  }
}

.categoryCardWrap .categoryCard_imgWrap img {
  max-width: 100%;
  max-height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  vertical-align: middle;
}

@media (max-width: 800px) {
  .categoryCardWrap .categoryCard_imgWrap img {
    max-width: 70%;
    max-height: 70%;
  }
}
