@charset "UTF-8";
.productCard {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 354px;
  background-color: #f5f7ff;
  border-radius: 20px;
  padding: 15px;
  max-width: 300px;
  min-width: 220px;
  font-weight: 600;
  font-size: 18px;
}

@media (max-width: 800px) {
  .productCard {
    font-size: 14px;
    height: 230px;
    padding: 10px;
    min-width: 152px;
  }
}

.productCard .img {
  position: relative;
  height: 180px;
  margin-bottom: 24px;
  text-align: center;
  background-color: white;
  border-radius: 8px;
  padding: 10px;
}

@media (max-width: 800px) {
  .productCard .img {
    height: 112px;
    margin-bottom: 5px;
    padding: 5px;
  }
}

.productCard .img .sale {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #fa5a2a;
  font-weight: 600;
  font-size: 14px;
  padding: 4px 12px;
  border-radius: 8px;
  color: #ffffff;
}

@media (max-width: 800px) {
  .productCard .img .sale {
    font-size: 10px;
    top: 5px;
    left: 5px;
  }
}

.productCard .img .favourites,
.productCard .img .favourites_active {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 26px;
}

@media (max-width: 800px) {
  .productCard .img .favourites,
  .productCard .img .favourites_active {
    top: 5px;
    right: 5px;
  }
}

.productCard .img .favourites {
  background: url("/Server/data/htdocs/DrSmarton/public/source/img/icon/favourites.svg") no-repeat;
  background-position: 0 0;
}

@media (max-width: 800px) {
  .productCard .img .favourites {
    background-position: 5px 0;
    background-size: 12px auto;
  }
}

.productCard .img .favourites_active {
  background: url("/Server/data/htdocs/DrSmarton/public/source/img/icon/favouritesActive.svg") no-repeat;
  background-position: 3px 3px;
}

@media (max-width: 800px) {
  .productCard .img .favourites_active {
    background-position: 5px 0;
    background-size: 12px auto;
  }
}

.productCard .img img {
  max-width: 100%;
  max-height: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  vertical-align: middle;
}

.productCard .info {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100px;
  border-radius: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

@media (max-width: 800px) {
  .productCard .info {
    height: 84px;
  }
}

.productCard .info .info__header {
  -webkit-box-flex: 1;
      -ms-flex: 1 1;
          flex: 1 1;
}

.productCard .info .info__header .name {
  margin-top: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.productCard .info .info__header .brand {
  margin-top: 2px;
  letter-spacing: 0.04em;
  font-size: 14px;
  font-weight: 400;
  color: #cad0dc;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 800px) {
  .productCard .info .info__header .brand {
    font-size: 12px;
  }
}

.productCard .info .info__header .oldPrice {
  margin-top: 6px;
  font-size: 12px;
  font-weight: 400;
  color: #9099ad;
  text-decoration: line-through;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 800px) {
  .productCard .info .info__header .oldPrice {
    font-size: 10px;
  }
}

.productCard .info .info__footer {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.productCard .info .info__footer .price::after {
  content: " ₽";
}

.productCard .info .info__footer .btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border-radius: 5px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@media (max-width: 800px) {
  .productCard .info .info__footer .btn {
    width: 18px;
    height: 18px;
  }
}
