@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";
.search {
    position: relative;
    flex: 1;
    margin: 0 50px;
    max-width: 670px;
    @media (max-width: breakpoints950) {
        margin: 0 20px;
    }
    @media (max-width: $breakpoints768) {
        margin: 0;
        max-width: 100%;
    }
    .searchBtn {
        position: absolute;
        left: 12px;
        top: 50%;
        margin-top: -10px;
    }
    input {
        box-sizing: border-box;
        width: 100%;
        padding: 11px 32px 12px 44px;
        border: none;
        border-radius: 10px;
        background-color: $light;
        color: black;
    }
}
