@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.productCard {
    box-sizing: border-box;
    height: 354px;
    background-color: $light;
    border-radius: 20px;
    padding: 15px;
    max-width: 300px;
    min-width: 220px;
    font-weight: 600;
    font-size: 18px;
    @media (max-width: $breakpoints768) {
        font-size: 14px;
        height: 230px;
        padding: 10px;
        min-width: 152px;
    }
    .img {
        position: relative;
        height: 180px;
        margin-bottom: 24px;
        text-align: center;
        background-color: white;
        border-radius: 8px;
        padding: 10px;
        @media (max-width: $breakpoints768) {
            height: 112px;
            margin-bottom: 5px;
            padding: 5px;
        }
        .sale {
            position: absolute;
            top: 10px;
            left: 10px;
            background-color: $orange;
            font-weight: 600;
            font-size: 14px;
            padding: 4px 12px;
            border-radius: 8px;
            color: #ffffff;
            @media (max-width: $breakpoints768) {
                font-size: 10px;
                top: 5px;
                left: 5px;
            }
        }
        .favourites,
        .favourites_active {
            position: absolute;
            top: 10px;
            right: 10px;
            width: 20px;
            height: 26px;
            @media (max-width: $breakpoints768) {
                top: 5px;
                right: 5px;
            }
        }
        .favourites {
            background: url($root + "/source/img/icon/favourites.svg") no-repeat;
            background-position: 0 0;
            @media (max-width: $breakpoints768) {
                background-position: 5px 0;
                background-size: 12px auto;
            }
        }
        .favourites_active {
            background: url($root + "/source/img/icon/favouritesActive.svg") no-repeat;
            background-position: 3px 3px;
            @media (max-width: $breakpoints768) {
                background-position: 5px 0;
                background-size: 12px auto;
            }
        }
        img {
            max-width: 100%;
            max-height: 100%;
            height: 100%;
            object-fit: contain;
            vertical-align: middle;
        }
    }
    .info {
        box-sizing: border-box;
        height: 100px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        @media (max-width: $breakpoints768) {
            height: 84px;
        }
        .info__header {
            flex: 1 1;
            .name {
                margin-top: 0;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .brand {
                margin-top: 2px;
                letter-spacing: 0.04em;
                font-size: 14px;
                font-weight: 400;
                color: $lightGrey;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                @media (max-width: $breakpoints768) {
                    font-size: 12px;
                }
            }
            .oldPrice {
                margin-top: 6px;
                font-size: 12px;
                font-weight: 400;
                color: $grey;
                text-decoration: line-through;
                white-space: nowrap;
                overflow: hidden;
                @media (max-width: $breakpoints768) {
                    font-size: 10px;
                }
            }
        }

        .info__footer {
            width: 100%;
            display: flex;
            justify-content: space-between;
            .price {
                &::after {
                    content: " ₽";
                }
            }
            .btn {
                display: flex;
                width: 24px;
                height: 24px;
                background-color: #ffffff;
                border-radius: 5px;
                justify-content: center;
                align-items: center;
                @media (max-width: $breakpoints768) {
                    width: 18px;
                    height: 18px;
                }
            }
        }
    }
}
