@charset "UTF-8";
.product {
  margin-top: 32px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

@media (max-width: 800px) {
  .product {
    display: none;
  }
}

.product .photo {
  -ms-flex-preferred-size: 400px;
      flex-basis: 400px;
  margin-right: 100px;
}

@media (max-width: 1200px) {
  .product .photo {
    -ms-flex-preferred-size: 300px;
        flex-basis: 300px;
    margin-right: 40px;
  }
}

.product .photo .mainPhoto {
  height: 400px;
  text-align: center;
}

.product .photo .mainPhoto img {
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  height: 100%;
}

.product .photo .thumbnailBar {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[4];
      grid-template-columns: repeat(4, 1fr);
  margin-top: 15px;
}

.product .photo .thumbnailBar :global(.active) {
  border: 1px solid #cad0dc !important;
}

.product .photo .thumbnailBar .thumbnail {
  width: 68px;
  height: 68px;
  padding: 5px;
  border-radius: 7px;
  border: 1px solid white;
}

.product .photo .thumbnailBar .thumbnail .item {
  height: 100%;
  text-align: center;
  cursor: pointer;
}

.product .photo .thumbnailBar .thumbnail .item img {
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  height: 100%;
}

.product .detail {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.product .detail h2 {
  margin-top: 10px;
}

@media (max-width: 1200px) {
  .product .detail h2 {
    font-size: 1.9rem;
  }
}

.product .detail .radioColor,
.product .detail .radioModel {
  margin-top: 24px;
}

.product .detail .modelWrap .model {
  margin-right: 84px;
}

.product .detail .modelWrap .model,
.product .detail .modelWrap .color {
  vertical-align: top;
  display: inline-block;
  margin-top: 36px;
}

.product .detail .priceWrap {
  margin-top: 78px;
}

.product .detail .priceWrap .price {
  display: inline-block;
  margin-right: 32px;
}

.product .detail .priceWrap .price::after {
  content: " ₽";
}

.product .detail .priceWrap .sale {
  display: inline-block;
  margin-right: 32px;
  font-size: 1.5rem;
  color: #fa5a2a;
}

.product .detail .priceWrap .oldPrice {
  display: inline-block;
  text-decoration: line-through;
  color: #cad0dc;
}

.product .detail .mainBtnWrap {
  margin-top: 16px;
}

.product .detail .mainBtnWrap :global(.addBtn) {
  vertical-align: top;
  margin-right: 32px;
}

@media (max-width: 920px) {
  .product .detail .mainBtnWrap :global(.addBtn) {
    margin-right: 20px;
  }
}

.product .detail .mainBtnWrap .favorite {
  cursor: pointer;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  vertical-align: top;
  width: 50px;
  height: 54px;
  background-color: #f5f7ff;
  border-radius: 8px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.product .detail .mainBtnWrap .favorite:hover {
  background-color: #edf0fc;
}

.stockWrap {
  margin-top: 46px;
}

.productMobile {
