.mobileBtnSection {
  display: none;
}

@media (max-width: 800px) {
  .mobileBtnSection {
    display: block;
  }
}

.mobileBtnSection .stockWrap {
  margin-top: 36px;
  font-size: 16px;
}

.mobileBtnSection .mainBtnWrap {
  margin-top: 36px;
  margin-bottom: 36px;
}

.mobileBtnSection .mainBtnWrap .mainBtn {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
}

@media (max-width: 480px) {
  .mobileBtnSection .mainBtnWrap .mainBtn {
    padding-right: 30px;
    padding-left: 30px;
  }
}

.info {
  margin-top: 50px;
}

.info :global(.active) {
  color: #2e3563;
}

.info h3 {
  cursor: pointer;
  display: inline-block;
  margin-right: 60px;
  color: grey;
}

@media (max-width: 480px) {
  .info h3 {
    font-size: 16px;
    margin-right: 36px;
    padding: 0 0 20px 0;
  }
}

.info .body {
  margin-top: 20px;
}

@media (max-width: 480px) {
  .info .body {
    margin-top: 0;
  }
}

.radioModel {
  margin-bottom: -16px;
}

.radioModel :global(.active) {
  background-color: #fa5a2a;
  color: white;
}

.radioModel div {
  cursor: pointer;
  display: inline-block;
  padding: 8px 20px;
  background-color: #f5f7ff;
  border-radius: 8px;
  color: #2e3563;
  word-spacing: normal;
  margin-right: 16px;
  margin-bottom: 16px;
}

.radioModel div:last-child {
  margin-right: 0;
}

.radioColor :global(.active) {
  border: 2px solid black;
}

.radioColor div {
  cursor: pointer;
  display: inline-block;
  width: 34px;
  height: 34px;
  border-radius: 19px;
  border: 2px solid white;
  margin-right: 20px;
}

.radioColor div:last-child {
  margin-right: 0;
}

.radioColor .red {
  background-color: #bb4443;
}

.radioColor .green {
  background-color: #a1c964;
}

.radioColor .blue {
  background-color: #878ec0;
}

.radioColor .white {
  background-color: #f7f7f7;
}

.radioColor .black {
  background-color: #3a3a3a;
}

.radioColor .purple {
  background-color: #5b0082;
}
