@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.feedbackHeader {
    display: flex;
    margin: 0 0 16px 0;
    overflow: hidden;
    .avatar {
        width: 65px;
        height: 65px;
        border-radius: 33px;
        overflow: hidden;
        display: flex;
        background-color: #dfe1e8;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
        @media (max-width: $breakpoints480) {
            width: 40px;
            height: 40px;
            border-radius: 20px;
        }
    }
    .username {
        font-size: 1.5rem;
        width: 10px;
        font-size: 1.5rem;
        width: 20px;
        max-height: 64px;
        @media (max-width: $breakpoints480) {
            font-size: 16px;
        }
    }
}

.feedback {
    box-sizing: border-box;
    width: calc((100% - 60px) / 3);
    background-color: $light;
    min-height: 300px;
    max-height: 440px;
    padding: 20px;
    border-radius: 10px;
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    @media (max-width: 1000px) {
        width: 100%;
        margin: 0;
        margin-bottom: 20px;
        min-height: 0;
    }

    .body {
        flex: 1;
        overflow: hidden;
        max-height: 175px;
        @media (max-width: $breakpoints480) {
            max-height: 72px;
            text-overflow: ellipsis;
            &.open {
                max-height: none;
            }
        }
    }

    .footer {
        position: relative;
        margin: 16px 0 0 0;
        text-align: right;
        color: $grey;
        font-size: 14px;
        .openBtn {
            position: absolute;
            left: 0;
            border-bottom: 1px solid $grey;
        }
    }
}
