@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.breadCrumps,
.feedbackH2 {
    @media (max-width: $breakpoints768) {
        display: none;
    }
}
.addFeedbackPopup {
    box-sizing: border-box;
    padding: 20px;
    border-radius: 11px;
    background-color: white;
    max-width: 680px;
    min-width: 300px;
    flex: 1;
    @media (max-width: $breakpoints480) {
        padding: 10px;
    }
    .addCommentInput {
        width: 100%;
        margin: 20px 0;
        @media (max-width: $breakpoints480) {
            margin: 0 0 20px 0;
        }
    }
    .addCommentText {
        width: 100%;
        margin-bottom: 30px;
        height: 150px;
    }
    :global(.mainBtn) {
        width: 100%;
    }
}
.feedbackSection {
    height: 315px;
    @media (max-width: 1000px) {
        height: auto;
    }
    .feedbackWrap {
        display: flex;
        justify-content: flex-start;
        margin: 0 -10px;
        margin-top: 36px;
        @media (max-width: 1000px) {
            margin: 0;
            margin-top: 36px;
            flex-direction: column;
            align-items: stretch;
        }
    }
}
.controlSection {
    display: flex;
    margin-top: 36px;
    @media (max-width: $breakpoints768) {
        flex-direction: column-reverse;
        margin-top: 10px;
    }
    .paginationBlock {
        flex: 1 1;
        display: flex;
        align-items: center;
        justify-content: end;
        @media (max-width: $breakpoints768) {
            margin-bottom: 30px;
            justify-content: center;
        }
    }
}
