@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.accordion {
    .header {
        display: flex;
        padding: 10px 20px;
        .title {
            flex: 1;
        }
        .arrow {
            display: flex;
            align-items: center;
            img {
                width: 14px;
                height: auto;
            }
        }
    }
    .open {
        background-color: $light;
    }
    .subCat {
        font-size: 14px;
        color: $darkGrey;
        a {
            display: block;
            padding: 10px 20px 10px 30px !important;
        }
    }
}
