.accordion .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 20px;
}

.accordion .header .title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.accordion .header .arrow {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.accordion .header .arrow img {
  width: 14px;
  height: auto;
}

.accordion .open {
  background-color: #f5f7ff;
}

.accordion .subCat {
  font-size: 14px;
  color: #687082;
}

.accordion .subCat a {
  display: block;
  padding: 10px 20px 10px 30px !important;
}
