@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.contacts {
    margin-top: 32px;
    display: flex;
    @media (max-width: $breakpoints768) {
        margin-top: 27px;
        flex-direction: column;
    }
    .mapWrap {
        flex: 1;
        margin-right: 60px;
        overflow: hidden;
        @media (max-width: $breakpoints768) {
            margin: 0 -30px;
        }
        background: url($root + "/source/img/map.png") no-repeat;
        background-size: 100%;
        @media (max-width: $breakpoints768) {
            width: 120%;
            flex-basis: 345px;
        }
        .YMaps {
            max-width: $breakpoints768;
            min-width: 280px;
            height: 550px;
            @media (max-width: $breakpoints768) {
                height: 345px;
            }
        }
    }
    .contactsSection {
        margin-top: -20px;
        flex: 1;
        color: $corp_inst;
        h4 {
            padding: 0 0 5px 0;
        }
        @media (max-width: $breakpoints768) {
            margin-top: 0px;
        }
        .row {
            display: flex;
            max-width: 420px;
            min-width: 280px;
            justify-content: space-between;
            padding: 5px 0;

            .left {
                margin-right: 20px;
                color: $corpLight;
            }
            .right {
                text-align: right;
            }
        }
    }
}
