@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.blackFont {
    color: black;
}
.banner {
    border-radius: 20px;
    height: 300px;
    margin-top: 32px;
    background: linear-gradient(280.53deg, #b899fb 51.64%, #919cf8 95.61%);
    @media (max-width: $breakpoints768) {
        margin-top: 25px;
        height: 148px;
    }
}
.homeH2 {
    @media (max-width: $breakpoints768) {
        display: none;
    }
}

.categoryCardWrap {
    display: grid;
    justify-items: stretch;
    margin-top: 32px;
    gap: 10px;
    grid-template-columns: repeat(5, 1fr);
    @media (max-width: $breakpoints768) {
        font-size: 13px;
        margin-top: 20px;
    }
    @media (max-width: $breakpoints480) {
        font-size: 10px;
        overflow: hidden;
    }
    .categoryCard_item {
        height: 126px;
        cursor: pointer;
        box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.08), 0px 2px 6px rgba(130, 169, 188, 0.08);
        text-align: center;
        overflow: hidden;
        padding: 0 10px;
        @media (max-width: $breakpoints768) {
            box-shadow: none;
            height: auto;
            padding: 0;
        }
        &:hover {
            box-shadow: 0px 5px 50px rgba(0, 67, 101, 0.16), 0px 2px 6px rgba(130, 169, 188, 0.16);
            @media (max-width: $breakpoints768) {
                box-shadow: none;
            }
        }
        span {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            @media (max-width: $breakpoints768) {
                white-space: normal;
            }
        }
    }
    .categoryCard_imgWrap {
        height: 60px;
        width: 110px;
        line-height: 60px;
        margin: 15px auto 13px auto;
        @media (max-width: $breakpoints768) {
            width: 60px;
            background-color: $light;
            border-radius: 30px;
            margin: 0 auto 4px auto;
        }
        img {
            @media (max-width: $breakpoints768) {
                max-width: 70%;
                max-height: 70%;
            }
            max-width: 100%;
            max-height: 100%;
            object-fit: contain;
            vertical-align: middle;
        }
    }
}
