@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.addBtn {
    user-select: none;
    object-fit: contain;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    @media (max-width: $breakpoints768) {
        width: 100%;
    }
    .controlWrap {
        width: 220px;
        display: flex;
        @media (max-width: 920px) {
            width: 240px;
        }
        @media (max-width: $breakpoints768) {
            width: 100%;
        }
        .control {
            cursor: pointer;
            display: inline-flex;
            vertical-align: top;
            font-size: 30px;
            line-height: 30px;
            width: 54px;
            height: 50px; // менять только вместе с высотой mainBtn
            padding: 0 0 5px 0; // менять только вместе с высотой mainBtn
            background-color: $light;
            border-radius: 8px;
            @media (max-width: $breakpoints768) {
                height: 49px;
            }
            @media (max-width: $breakpoints480) {
                height: 43px;
            }
            &:hover {
                background-color: $lightHover;
            }
            border-radius: 8px 0 0 8px;
            &:last-child {
                border-radius: 0 8px 8px 0;
            }
            justify-content: center;
            align-items: center;
        }
        .count {
            display: flex;
            justify-content: center;
            align-items: center;
            flex: 1;
            font-size: 17px;
            // background-color: $light;
            &::after {
                content: " шт";
            }
        }
    }
}
