.breadCrumbs {
  color: #9099ad;
  font-weight: 400;
  font-size: 14px;
  margin-top: 53px;
}

@media (max-width: 800px) {
  .breadCrumbs {
    display: none;
  }
}

.breadCrumbs a {
  color: inherit;
  cursor: pointer;
}

.breadCrumbs a:hover {
  color: #2e3563;
}

.breadCrumbs div {
  cursor: default;
  display: inline-block;
  position: relative;
  padding: 0 19px 0 0;
}

.breadCrumbs div::after {
  content: " ";
  display: inline-block;
  position: absolute;
  top: 3px;
  right: 5px;
  width: 11px;
  height: 15px;
  background: url("/Server/data/htdocs/DrSmarton/public/source/img/icon/rightArrow.svg") no-repeat;
  background-size: 6px auto;
  background-position: 3px 3px;
}

.breadCrumbs div:last-child::after {
  display: none !important;
}
