@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";
.pagination {
    user-select: none;
    display: inline-block;
    border: 1px solid $lightGrey;
    border-radius: 8px;
    cursor: pointer;
    color: $darkGrey;
    line-height: 0;
    white-space: nowrap;
    @media (max-width: $breakpoints768) {
        max-width: 100%;
        display: inline-flex;
    }
    .control {
        display: inline-flex;
        vertical-align: top;
        justify-content: center;
        align-items: center;
        width: 48px;
        height: 32px;
    }
    .sliderSection {
        display: inline-block;
        position: relative;
        max-width: 336px;
        overflow: hidden;
        flex: 1;
        @media (max-width: $breakpoints768) {
            max-width: none;
        }
        .slider {
            white-space: nowrap;
            transition: margin 0.5s;
            div {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                width: 48px;
                height: 32px;
                &.active {
                    background-color: $lightGrey;
                }
            }
        }
    }
}
