.search {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0 50px;
  max-width: 670px;
}

@media (max-width: breakpoints950) {
  .search {
    margin: 0 20px;
  }
}

@media (max-width: 800px) {
  .search {
    margin: 0;
    max-width: 100%;
  }
}

.search .searchBtn {
  position: absolute;
  left: 12px;
  top: 50%;
  margin-top: -10px;
}

.search input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding: 11px 32px 12px 44px;
  border: none;
  border-radius: 10px;
  background-color: #f5f7ff;
  color: black;
}
