.goodsSlider {
  display: -ms-grid;
  display: grid;
  justify-items: stretch;
  margin-top: 32px;
  gap: 10px;
  overflow-x: hidden;
  -ms-grid-columns: (1fr)[5];
      grid-template-columns: repeat(5, 1fr);
}

@media (max-width: 800px) {
  .goodsSlider {
    margin-top: 15px;
    overflow: scroll;
  }
}
