.pagination {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  border: 1px solid #cad0dc;
  border-radius: 8px;
  cursor: pointer;
  color: #687082;
  line-height: 0;
  white-space: nowrap;
}

@media (max-width: 800px) {
  .pagination {
    max-width: 100%;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
  }
}

.pagination .control {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  vertical-align: top;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 48px;
  height: 32px;
}

.pagination .sliderSection {
  display: inline-block;
  position: relative;
  max-width: 336px;
  overflow: hidden;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

@media (max-width: 800px) {
  .pagination .sliderSection {
    max-width: none;
  }
}

.pagination .sliderSection .slider {
  white-space: nowrap;
  -webkit-transition: margin 0.5s;
  transition: margin 0.5s;
}

.pagination .sliderSection .slider div {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 48px;
  height: 32px;
}

.pagination .sliderSection .slider div.active {
  background-color: #cad0dc;
}
