@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $ebonite;
}
body {
    @media (max-width: $breakpoints480) {
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
    }
}
input,
textarea {
    box-sizing: border-box;
    background-color: $light;
    padding: 8px 15px;
    border-radius: 10px;
    border: 1px solid $light;
    &.error {
        color: $orange;
        border: 1px solid $orange;
    }
}
input::placeholder {
    color: $grey;
}
p {
    margin-top: 30px;
    line-height: 24px;
    @media (max-width: $breakpoints480) {
        line-height: 18px;
    }
}
.h2MarginTop {
    margin-top: 50px;
    @media (max-width: $breakpoints768) {
        margin-top: 20px;
    }
}

.container {
    margin: 0 auto;
    padding: 0 60px;
    max-width: 1440px;
    @media (max-width: $breakpoints768) {
        padding: 0 30px;
    }
    @media (max-width: $breakpoints480) {
        padding: 0 16px;
    }
}

.home_header {
    margin-top: 30px;
}

.sliderTitle {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    @media (max-width: $breakpoints768) {
        margin-top: 28px;
    }
    h2 {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-right: 20px;
        @media (max-width: $breakpoints768) {
            font-size: 1.65rem;
        }
        @media (max-width: $breakpoints480) {
            font-size: 1.125rem;
        }
    }
    span {
        white-space: nowrap;
        cursor: pointer;
        font-size: 1.5rem;
        color: $corp_inst;
        &:hover {
            color: $corp_inst_hover;
        }
        @media (max-width: $breakpoints768) {
            font-size: 1rem;
        }
        @media (max-width: $breakpoints480) {
            font-size: 0.625rem;
        }
    }
}
.mainBtn {
    display: block;
    cursor: pointer;
    text-align: center;
    user-select: none;
    background: linear-gradient(90deg, #436397 0%, #7dafff 100%);
    border-radius: 8px;
    padding: 15px 88px;
    color: white;
    &:hover {
        background: linear-gradient(90deg, #37507a 0%, #6e9ae0 100%);
    }
    @media (max-width: 920px) {
        padding-right: 40px;
        padding-left: 40px;
    }
}

@media (max-width: $breakpoints480) {
    h1,
    .h1 {
        font-size: 2.8rem;
    }
    h2,
    .h2 {
        font-size: 1.33rem;
        font-weight: 500;
    }
    h3,
    .h3 {
        font-size: 1.125rem;
        font-weight: 500;
        margin-top: 17px;
    }
    h4,
    .h4 {
        font-size: 0.875rem;
        font-weight: 400;
        margin-top: 11px;
    }
    h5,
    .h5 {
        font-size: 0.5rem;
    }
}
