@media (max-width: 480px) {
  .stockWrap {
    font-size: 14px;
  }
}

.stockWrap .lineIndicator {
  display: inline-block;
  margin-right: 32px;
}

.stockWrap .lineIndicator div {
  display: inline-block;
  vertical-align: baseline;
  width: 50px;
  height: 8px;
  margin-right: 4px;
  background-color: #f5f7ff;
}

.stockWrap .lineIndicator div.active {
  background-color: #34d18f;
}

@media (max-width: 480px) {
  .stockWrap .lineIndicator div {
    width: 35px;
  }
}

.stockWrap .description {
  display: inline-block;
  white-space: nowrap;
  margin-top: 5px;
}
