.footer {
  overflow: hidden;
  margin-top: 155px;
  border-top: 1px solid #bad0de;
  border-bottom: 1px solid #bad0de;
  font-weight: 400;
  letter-spacing: 0.04em;
  padding: 30px 0 20px 0;
}

.footer h4 {
  cursor: pointer;
  padding: 4px 0;
}

.footer h4:hover {
  color: #2e3563;
}

.footer li {
  cursor: pointer;
  padding: 4px 0;
  color: #9099ad;
}

.footer li:hover {
