@media (max-width: 800px) {
  .breadCrumps,
  .feedbackH2 {
    display: none;
  }
}

.addFeedbackPopup {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
  border-radius: 11px;
  background-color: white;
  max-width: 680px;
  min-width: 300px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

@media (max-width: 480px) {
  .addFeedbackPopup {
    padding: 10px;
  }
}

.addFeedbackPopup .addCommentInput {
  width: 100%;
  margin: 20px 0;
}

@media (max-width: 480px) {
  .addFeedbackPopup .addCommentInput {
    margin: 0 0 20px 0;
  }
}

.addFeedbackPopup .addCommentText {
  width: 100%;
  margin-bottom: 30px;
  height: 150px;
}

.addFeedbackPopup :global(.mainBtn) {
  width: 100%;
}

.feedbackSection {
  height: 315px;
}

@media (max-width: 1000px) {
  .feedbackSection {
    height: auto;
  }
}

.feedbackSection .feedbackWrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin: 0 -10px;
  margin-top: 36px;
}

@media (max-width: 1000px) {
  .feedbackSection .feedbackWrap {
    margin: 0;
    margin-top: 36px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: stretch;
        -ms-flex-align: stretch;
            align-items: stretch;
  }
}

.controlSection {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 36px;
}

@media (max-width: 800px) {
  .controlSection {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    margin-top: 10px;
  }
}

.controlSection .paginationBlock {
  -webkit-box-flex: 1;
      -ms-flex: 1 1;
          flex: 1 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: end;
}

@media (max-width: 800px) {
  .controlSection .paginationBlock {
    margin-bottom: 30px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}
