.deliverWrap {
  color: #2e3563;
}

.deliverWrap h4 {
  max-width: 345px;
}

.deliverWrap ul {
  margin-top: 16px;
  list-style: inside;
  font-weight: 500;
  font-size: 16px;
  color: #878ec0;
}

@media (max-width: 480px) {
  .deliverWrap ul {
    font-size: 14px;
    margin-top: 22px;
    margin-bottom: 22px;
  }
}

.deliverWrap .deliverSection {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #2e3563;
}

@media (max-width: 800px) {
  .deliverWrap .deliverSection {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.deliverWrap .deliverSection .delivery {
  margin-right: 120px;
}

@media (max-width: 1000px) {
  .deliverWrap .deliverSection .delivery {
    margin-right: 60px;
  }
}

@media (max-width: 800px) {
  .deliverWrap .deliverSection .delivery {
    margin-right: 0;
  }
}

.deliverWrap .attention {
  color: #fa5a2a;
}
