@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.footer {
    overflow: hidden;
    margin-top: 155px;
    border-top: 1px solid #bad0de;
    border-bottom: 1px solid #bad0de;
    font-weight: 400;
    letter-spacing: 0.04em;
    padding: 30px 0 20px 0;
    h4 {
        cursor: pointer;
        padding: 4px 0;
        &:hover {
            color: $corp_inst;
        }
    }
    li {
        cursor: pointer;
        padding: 4px 0;
        color: $grey;
        &:hover {
            color: $corp_inst;
        }
    }

    .footer__innerWrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 -35px;
        & > div {
            margin: 0 35px 20px 35px;
        }
        .footer__social {
            flex-basis: 328px;
            margin-top: 20px;
            @media (max-width: 1500px) {
                flex-basis: 280px;
            }
            .footer__fullAddress {
                margin-top: 29px;
                line-height: 24px;
            }
            .socialMediaIcon {
                margin-top: 24px;
            }
        }
        .footer__contacts {
            flex-basis: 195px;
            @media (max-width: 1150px) {
                flex-basis: 334px;
            }
        }
        .footer__catalog {
            ul {
                columns: 2;
                column-gap: 59px;
                @media (max-width: 1500px) {
                    columns: 1;
                }
                @media (max-width: 1150px) {
                    columns: 2;
                }
            }
        }
        .footer__menu {
            ul {
                columns: 2;
                column-gap: 40px;
                @media (max-width: 1500px) {
                    columns: 1;
                }
                @media (max-width: 1150px) {
                    columns: 2;
                }
            }
        }
    }
}
.privacyPolicy {
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.04em;
    display: flex;
    justify-content: space-between;
    padding: 4px 0 6px 0;
    flex-wrap: wrap;
}
