.feedbackHeader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 16px 0;
  overflow: hidden;
}

.feedbackHeader .avatar {
  width: 65px;
  height: 65px;
  border-radius: 33px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #dfe1e8;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 10px;
}

.feedbackHeader .avatar img {
  max-width: 100%;
  max-height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

@media (max-width: 480px) {
  .feedbackHeader .avatar {
    width: 40px;
    height: 40px;
    border-radius: 20px;
  }
}

.feedbackHeader .username {
  font-size: 1.5rem;
  width: 10px;
  font-size: 1.5rem;
  width: 20px;
  max-height: 64px;
}

@media (max-width: 480px) {
  .feedbackHeader .username {
    font-size: 16px;
  }
}

.feedback {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: calc((100% - 60px) / 3);
  background-color: #f5f7ff;
  min-height: 300px;
  max-height: 440px;
  padding: 20px;
  border-radius: 10px;
  margin: 0 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

@media (max-width: 1000px) {
  .feedback {
    width: 100%;
    margin: 0;
    margin-bottom: 20px;
    min-height: 0;
  }
}

.feedback .body {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: hidden;
  max-height: 175px;
}

@media (max-width: 480px) {
  .feedback .body {
    max-height: 72px;
    text-overflow: ellipsis;
  }
  .feedback .body.open {
    max-height: none;
  }
}

.feedback .footer {
  position: relative;
  margin: 16px 0 0 0;
  text-align: right;
  color: #9099ad;
  font-size: 14px;
}

.feedback .footer .openBtn {
  position: absolute;
  left: 0;
  border-bottom: 1px solid #9099ad;
}
