@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.breadCrumbs {
    color: $grey;
    font-weight: 400;
    font-size: 14px;
    margin-top: 53px;
    @media (max-width: $breakpoints768) {
        display: none;
    }
    a {
        color: inherit;
        cursor: pointer;
        &:hover {
            color: $corp_inst;
        }
    }
    & div {
        cursor: default;
        display: inline-block;
        position: relative;
        padding: 0 19px 0 0;
        &::after {
            content: " ";
            display: inline-block;
            position: absolute;
            top: 3px;
            right: 5px;
            width: 11px;
            height: 15px;
            background: url($root + "/source/img/icon/rightArrow.svg") no-repeat;
            background-size: 6px auto;
            background-position: 3px 3px;
        }
        &:last-child {
            &::after {
                display: none !important;
            }
        }
    }
}
