@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.mobileMenu {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 15;
    background-color: #00000040;
    color: $corp_inst;
    @media (max-width: $breakpoints768) {
        display: block;
    }
    .menu {
        box-sizing: border-box;
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 0;
        left: 0;
        bottom: 0;
        width: 276px;
        background-color: white;
        padding: 25px;
        .header {
            display: flex;
            margin-bottom: 20px;
            .avatar {
                margin-right: 11px;
                display: flex;
                align-items: center;
                padding: 1px 0 0 0;
                width: 32px;
            }
            .username {
                font-size: 14px;
                width: 20px;
            }
        }
        .body {
            flex: 1 1;
            overflow-y: scroll;
            .linkSection {
                flex: 1;
                overflow-y: scroll;
                a,
                div {
                    display: block;
                    font-weight: 600;
                    font-size: 18px;
                    padding: 15px 0;
                    border-right: 4px solid white;
                }
                :global(.current) {
                    color: #b899fb;
                    border-right: 4px solid #b899fb;
                }
            }
        }

        .footer {
            background-color: #ffffff;
            padding-top: 20px;
            img {
                width: 152px;
                height: auto;
                margin-bottom: 10px;
            }
        }
        .catalogWrap {
            position: absolute;
            display: flex;
            flex-direction: column;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: white;
            font-weight: 600;
            font-size: 18px;
            .searchWrap {
                padding: 20px;
            }
            .backBtn {
                margin-top: 15px;
                margin-left: 20px;
                margin-bottom: 10px;
                cursor: pointer;
                img {
                    width: 19px;
                    height: auto;
                    margin-right: 10px;
                    vertical-align: middle;
                }
            }
            .catalog {
                flex: 1;
                overflow-y: scroll;
                margin-top: 15px;
                a {
                    padding-left: 20px;
                }
            }
            a {
                display: block;
                padding: 10px 0;
            }
            .footer {
                padding: 20px 20px 27px 20px;
            }
        }
    }
}

.mobileNavigationBar {
    display: none;
    margin-top: 20px;
    margin-bottom: 39px;
    & > div {
        display: flex;
        align-items: center;
    }
    .logo {
        flex: 1 1;
        justify-content: center;
        img {
            width: 152px;
        }
    }
}

.navigationBar {
    display: flex;
    .logo {
        width: 184px;
        height: 35px;
        margin: 13px 20px 0 0;
    }
    .navigation {
        user-select: none;
        color: $lightGrey;
        display: flex;
        flex-wrap: wrap;
        height: 19px;
        justify-content: flex-end;
        margin-left: auto;
        margin-top: 20px;
        overflow: hidden;
        padding: 4px;
        text-overflow: ellipsis;
        a {
            cursor: pointer;
            margin-left: 24px;
            color: inherit;
        }
        :global(.current) {
            color: $corp_inst;
            color: $corp_inst;
            border-bottom: 2px solid $corp_inst;
            margin-top: -2px;
            padding-bottom: 2px;
        }
        a:hover {
            color: $corp_inst;
        }
    }
}
.desktopHeader {
    margin-top: 22px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.catalogBtn {
    font-size: 14px;
    padding: 10px 38px 12px 42px;
    border-radius: 8px;
    color: white;
    background-color: $corp_inst;
    display: inline-block;
    cursor: pointer;
    &:hover {
        background-color: $corp_inst_hover;
    }
    img {
        padding: 0 16px 0 0;
    }
}

.navbar {
    display: flex;
    margin: 0 -25px;
    @media (max-width: breakpoints950) {
        margin: 0 -10px;
    }
    .item {
        height: 100%;
        display: flex;
        position: relative;
        align-items: center;
        cursor: pointer;
        margin: 0 25px;
        @media (max-width: breakpoints950) {
            margin: 0 10px;
        }

        div {
            position: absolute;
            top: -2px;
            left: 18px;
            font-size: 11px;
            font-weight: 700;
            background-color: $lightOrange;
            padding: 2px 6px 2px 6px;
            color: $orange;
            border-radius: 5px;
        }
    }
}

:global(.mobileNavBarPlug) {
    display: none;
}
:global(.mobileNavBarPlug),
.mobileNavBar {
    height: 84px;
}

.mobileNavBar {
    display: none;
    width: 100%;
    bottom: 0;
    position: fixed;
    background: #ffffff;
    box-shadow: 0px 0px 12px #d1d1d180;
    border-radius: 30px;
    justify-content: space-evenly;
    z-index: 10;
    .item {
        display: flex;
        position: relative;
        width: 50px;
        justify-content: center;
        align-items: center;
        :global(.current) {
            &::after {
                content: " ";
                position: absolute;
                bottom: 22px;
                right: calc(50% - 3px);
                background-color: $corp_inst;
                width: 6px;
                height: 6px;
                border-radius: 3px;
                box-shadow: 0 -19px 21px 4px #060a56c4;
            }
            svg {
                margin-top: -7px;
                path {
                    fill: $corp_inst;
                }
            }
        }
        &.active {
            &.mobileNavBar__item__basket {
                &::before {
                    left: calc(50% + 5px) !important;
                }
            }
            &.mobileNavBar__item__user {
                &::before {
                    left: calc(50% + 0px) !important;
                }
            }
            &::before {
                content: " ";
                position: absolute;
                top: 23px;
                left: calc(50% + 2px);
                background-color: $yellow;
                width: 10px;
                height: 10px;
                border-radius: 6px;
                border: 2px solid #ffffff;
            }
        }
    }
}
.closePlug {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.desktopCatalog {
    position: absolute;
    background-color: white;
    left: 0;
    right: 0;
    padding-top: 80px;
    padding-bottom: 80px;
    z-index: 5;
    box-shadow: 0 40px 30px 0 #0000000d;
    @media (max-width: $breakpoints768) {
        display: none;
    }
    .catalog {
        column-count: 4;
        div {
            page-break-inside: avoid;
            break-inside: avoid;
            float: left;
            .parent {
                font-size: 20px;
                padding-left: 0 !important;
                color: $corp_inst;
            }
            .aWrap {
                min-width: 300px;
                a {
                    padding-top: 10px;
                    padding-bottom: 10px;
                    padding-left: 10px;
                    display: inline-block;
                    color: $darkGrey;
                    &:hover {
                        color: $orange;
                    }
                }
            }
        }
    }
}

@media (max-width: $breakpoints768) {
    .catalogBtn,
    .navbar,
    .navigationBar {
        display: none;
    }
    .mobileNavigationBar,
    .mobileNavBar {
        display: flex;
    }
    :global(.mobileNavBarPlug) {
        display: block;
    }
}
