body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, 
pre, form, fieldset, input, textarea, p, blockquote, th, td { 
	padding: 0;
	margin: 0;
}
html, body{
    min-width: 320px;
    font-size: 16px;
    font-weight: 500;
    height: 100%;
}
a{
	color: inherit;
	text-decoration: none;
}
#root{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
	position: relative;
}
header{
    flex: none;
}
#content{
    flex: 1;
	overflow: hidden;
}
h1,.h1{
    font-size: 2.8rem;
} 
h2,.h2{
    font-size: 2.25rem;
    font-weight: 500;
	margin-top: 32px;
} 
h3,.h3{
    font-size: 1.5rem;
    font-weight: 600;
	margin-top: 30px;
} 
h4,.h4{ 
    font-size: 1.125rem;
    font-weight: 600;
	margin-top: 20px;
}
h5,.h5{
    font-size: 0.7rem;
}
textarea{
	resize: none;
}
input, .input, textarea{
    border:none;
    outline:none;
    font-size: 1rem;
}
fieldset, img { 
	border: 0;
	}
table {
	border-collapse: collapse;
	border-spacing: 0;
	}
ol, ul {
	list-style: none;
	}
address, caption, cite, code, dfn, em, strong, th, var {
	font-weight: normal;
	font-style: normal;
	}
caption, th {
	text-align: left;
	}

q:before, q:after {
	content: '';
	}
abbr, acronym { 
	border: 0;
	}
