@import "/Server/data/htdocs/DrSmarton/src/style/variables.scss";

.stockWrap {
    @media (max-width: $breakpoints480) {
        font-size: 14px;
    }
    .lineIndicator {
        display: inline-block;
        margin-right: 32px;
        div {
            display: inline-block;
            vertical-align: baseline;
            width: 50px;
            height: 8px;
            margin-right: 4px;
            background-color: $light;
            &.active {
                background-color: #34d18f;
            }
            @media (max-width: $breakpoints480) {
                width: 35px;
            }
        }
    }
    .description {
        display: inline-block;
        white-space: nowrap;
        margin-top: 5px;
    }
}
